:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html {
  background: var(--background);
  color: var(--foreground);
}
body {
  font-family: var(--font-manrope), sans-serif;
  padding: 16px;
  min-height: 100vh;
  align-items: center;
  margin: 0 auto;
}
main {
  display: flex;
  flex-direction: column;
  gap: 80px;
}
.heroContainer {
  margin: 0 auto;
  width: 100%;
  background-color: #161616;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 42px;
  height: calc(100vh - 32px);
  position: relative;
  overflow: hidden;
}
.heroContainer::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0.05) 55%, transparent 75%);
  pointer-events: none;
  z-index: 0;
}
.heroContainer>* {
  position: relative;
  z-index: 1;
}
.heroVideo {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}
.heroContainer>*:not(.heroVideo) {
  position: relative;
  z-index: 1;
}
.heroMainText {
  text-align: center;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.heroTitle {
  display: flex;
  gap: 12px;
  align-items: end;
}
.heroGlitch {
  position: relative;
  display: inline-block;
  color: #ffffff;
  font-size: clamp(34px, calc(1.5686vw + 29.88px), 60px) !important;
  line-height: 100%;
}
.dots {
  display: grid;
  grid-template-columns: repeat(2, 8px);
  grid-template-rows: repeat(2, 8px);
  gap: 8px;
  margin-bottom: 8px;
}
.dots span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #ffffff;
}
.heroMainText h2 {
  margin-top: 12px;
  font-size: clamp(16px, calc(0.1307vw + 15.49px), 18px) !important;
  line-height: 100%;
}
.sectionAnchor {
  scroll-margin-top: 96px;
  max-width: 1920px;
  margin: 0 auto;
  width: 100%;
}
.sectionFull {
  width: 100%;
}
.navDock {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 45;
  transition: top 0.3s ease, bottom 0.3s ease;
  display: flex;
  gap: 8px;
  align-items: center;
}
.navDock.is-hidden {
  pointer-events: none;
}
.navDockSide,
 .navDockCenter {
  display: flex;
  align-items: center;
}
.navDockCenter {
  gap: 8px;
}
.navDock--bottom {
  bottom: 30px;
  width: 100%;
  justify-content: center;
}
.navDock--top {
  top: 16px;
}
.navDockList {
  display: flex;
  gap: 8px;
  padding: 6px;
  border-radius: 999px;
  background: #d3d3d338;
  border: 1px solid #ffffff14;
  backdrop-filter: blur(12px);
}
.navMobDockList {
  display: none;
  gap: 8px;
  padding: 10px;
  border-radius: 999px;
  background: rgba(30, 30, 30, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(12px);
  width: 100%;
  max-width: 228px;
}
.navDockMenuButton {
  display: none;
  height: 36px;
  min-width: 140px;
  padding: 0 18px;
  border-radius: 999px;
  background: #c8ff2c;
  color: #0b0b0b;
  font-weight: 600;
}
.navDockMenuButton.is-open {
  background: #ffffff;
  color: #0b0b0b;
}
.navDockLink {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  padding: 0 14px;
  border-radius: 999px;
  color: #d7d7d7;
  text-decoration: none;
  font-size: 14px;
  z-index: 0;
}
.navDockPill {
  position: absolute;
  inset: 0;
  border-radius: 999px;
  background: #c8ff2c;
  z-index: -1;
}
.navDockLink.is-active {
  color: #0b0b0b;
}
.navOverlay {
  position: fixed;
  inset: 0;
  background: rgba(10, 10, 10, 0.78);
  backdrop-filter: blur(16px);
  z-index: 40;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 18px 18px 120px;
}
.navOverlayPanel {
  background: #ffffff30;
  border: 1px solid #ffffff14;
  border-radius: 12px;
  width: min(520px, 100%);
  padding: 12px;
  box-shadow: 0 24px 60px #00000073;
}
.navOverlayHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 18px;
}
.navOverlayList {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.navOverlayLink {
  color: #fff;
  background: #ffffff10;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 16px;
  text-decoration: none;
  display: block;
}
.navOverlayLink:hover {
  background: rgba(255, 255, 255, 0.16);
}
.navOverlayAccordion {
  width: 100%;
  padding: 0;
}
.navOverlayAccordionItem {
  background: none;
  border: none;
  padding: 0;
  box-shadow: none;
}
.navOverlayAccordionTrigger {
  padding: 12px 24px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.08);
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  transition: background 0.3s ease;
}
.navOverlayAccordionTrigger:hover {
  background: rgba(255, 255, 255, 0.16);
}
.navOverlayIndicator {
  display: block;
  width: 18px;
  height: 18px;
  transition: transform 0.3s ease;
  transform: rotate(0deg);
}
.navOverlayAccordionItem[data-open="true"] .navOverlayIndicator,
 .navOverlayAccordionItem [data-open="true"] .navOverlayIndicator {
  transform: rotate(180deg);
}
.navOverlayAccordionContent {
  padding: 10px 0 0;
}
.navOverlaySubList {
  gap: 12px;
  display: grid;
  background: #ffffff10;
  padding: 8px;
  border-radius: 8px;
}
.navOverlayServiceLink {
  font-size: 15px;
  padding: 10px 14px;
  border-radius: 12px;
}
.wpBtn,
 .callBtn {
  background-color: #000;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wpBtn {
  padding: 15px;
}
.callBtn {
  padding: 6px;
}
.callBtn img {
  width: 36px;
  height: 36px;
}
.container {
  max-width: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 42px;
}
.sectionTitle {
  display: flex;
  gap: 12px;
  font-size: clamp(18px, calc(0.1307vw + 17.49px), 20px) !important;
  align-items: center;
  line-height: 100%;
}
.sectionTitle .dots {
  margin: 0;
  gap: 5px;
}
.sectionTitle .dots span {
  width: 6px;
  height: 6px;
}
.servicesContainer {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 20px;
  width: 100%;
}
.serviceCard {
  position: relative;
  overflow: hidden;
  border-radius: 24px;
  padding: 28px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #f5f5f5;
  border: 1px solid #212121;
}
.serviceCard::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: var(--service-bg);
  background-size: cover;
  background-position: left center;
  transition: transform 0.6s ease;
  transform-origin: left center;
  border-radius: inherit;
  z-index: 0;
}
.serviceCard:hover::before {
  transform: scale(1.2);
}
.serviceCard>* {
  position: relative;
  z-index: 1;
}
.serviceHeader h3 {
  font-size: clamp(18px, calc(0.1307vw + 17.49px), 20px) !important;
  margin-bottom: 12px;
  text-align: center;
}
.serviceList {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.serviceList h4 {
  font-size: clamp(13px, calc(0.1961vw + 12.24px), 16px) !important;
  font-weight: 400;
  margin: 0;
}
.serviceListDot {
  width: 4px;
  height: 4px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.7);
}
.serviceCard a {
  width: 100%;
}
.serviceButton {
  width: 100%;
  font-size: clamp(16px, calc(0.2614vw + 14.98px), 20px) !important;
  height: 64px;
  font-weight: 600;
  transition: background 0.5s ease, color 0.5s ease;
}
.serviceCard:hover .serviceButton {
  background: #c8ff2c !important;
  color: #0b0b0b;
}
.servicePage {
  width: 100%;
}
.servicePageHero {
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
  gap: 32px;
  align-items: center;
  background: #101010;
  border-radius: 24px;
  padding: 32px;
}
.servicePageContent h1 {
  margin: 0 0 12px;
  color: #ffffff;
}
.servicePageContent p {
  margin: 0 0 16px;
  color: rgba(255, 255, 255, 0.75);
  line-height: 1.6;
}
.servicePageContent ul {
  margin: 0;
  padding-left: 18px;
  color: rgba(255, 255, 255, 0.8);
  display: grid;
  gap: 6px;
}
.servicePageImage {
  position: relative;
  width: 100%;
  aspect-ratio: 4 / 3;
  border-radius: 20px;
  overflow: hidden;
}
@media (max-width: 960px) {
  .servicePageHero {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 960px) {
  .servicesContainer {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (max-width: 640px) {
  .servicesContainer {
    grid-template-columns: 1fr;
  }
  .serviceIntro {
    max-height: 600px;
    padding: 12px;
    height: 100%;
  }
}
.offersContainer {
  position: relative;
  width: 100%;
}
.offersSticky {
  position: sticky;
  top: 0;
  height: 100vh;
  border-radius: 32px;
  overflow: hidden;
}
.offersMedia {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  overflow: hidden;
}
.offersCanvas {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}
.offersTrack {
  position: relative;
  z-index: 1;
  margin-top: -100vh;
}
.offersItem {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 48px;
}
.offersItemInner {
  max-width: 640px;
  text-align: center;
  padding: 24px 32px;
  border-radius: 24px;
}
.offersItem h4 {
  font-size: clamp(24px, calc(0.5229vw + 21.96px), 32px) !important;
  margin-bottom: 24px;
  line-height: 100%;
  color: #c8ff2c;
  text-transform: uppercase;
}
.offersItem p {
  margin: 0;
  color: #fff;
  font-size: clamp(16px, calc(0.1307vw + 15.49px), 18px) !important;
  max-width: 415px;
  width: 100%;
}
.casesContainer {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 8px;
  width: 100%;
}
.caseCard {
  border-radius: 24px;
  border: 1px solid #1f1f1f;
  padding: 28px;
  min-height: 747px;
  background-color: #101010;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.caseCard p {
  mix-blend-mode: difference;
}
.caseCard.is-cursor-active {
  cursor: none;
}
.caseCard::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: var(--case-bg);
  background-size: cover;
  background-position: left center;
  transform: scale(1);
  transition: opacity 0.5s ease;
  border-radius: inherit;
  z-index: 0;
}
.caseCard:hover::before {
  transform: scale(1);
}
.caseCard>* {
  position: relative;
  z-index: 1;
}
.caseCardFade {
  position: absolute;
  inset: 0;
  background: #000000;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
  pointer-events: none;
}
.caseCardFade.is-active {
  opacity: 1;
}
.caseCardHover {
  position: absolute;
  left: var(--cursor-x, 50%);
  top: var(--cursor-y, 50%);
  transform: translate(-50%, -50%);
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  border-radius: 6px;
  background: #c8ff2c;
  color: #0b0b0b;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  white-space: nowrap;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.35);
  opacity: 0;
  transition: opacity 0.2s ease;
  z-index: 2;
  pointer-events: none;
  width: 210px;
}
.caseCardHover img {
  width: 20px;
  height: 20px;
  object-fit: contain;
}
.caseCard.is-cursor-active .caseCardHover {
  opacity: 1;
}
.caseCardHeader {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
}
.caseCardHeaderRow {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.caseCardHeaderItem {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  border-radius: 8px;
  background: #ffffff1f;
  backdrop-filter: blur(20px);
  transition: background 0.4s ease, color 0.4s ease;
}
.caseCardHeaderItem img {
  width: 20px;
  height: 20px;
  object-fit: contain;
  transition: filter 0.4s ease;
}
.caseCardHeaderItem h3 {
  font-size: clamp(14px, calc(0.2614vw + 12.98px), 18px) !important;
  font-weight: 500;
  margin: 0;
  color: #ffffff;
  transition: color 0.4s ease;
}
.caseCard:hover .caseCardHeaderItem {
  background: #c8ff2c;
}
.caseCard:hover .caseCardHeaderItem h3 {
  color: #0b0b0b;
}
.caseCard:hover .caseCardHeaderItem img {
  filter: brightness(0) saturate(100%);
}
.caseCard p {
  font-size: clamp(24px, calc(0.5229vw + 21.96px), 32px);
  font-weight: 600;
  line-height: 100%;
  max-width: 366px;
  width: 100%;
}
@media (max-width: 960px) {
  .casesContainer {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 960px) {
  .offersSticky {
    height: 100vh;
    border-radius: 24px;
  }
  .offersTrack {
    margin-top: -70vh;
  }
  .offersItem {
    height: 100vh;
    padding: 24px;
  }
  .offersItem h4 {
    font-size: 24px;
  }
}
.contactsContainer {
  display: flex;
  gap: 18px;
  width: 100%;
  flex-direction: column;
}
.userInputs {
  display: flex;
  gap: 8px;
}
.textArea {
  width: 100%;
  height: 100%;
}
.userData {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.contactTextarea {
  height: 336px;
}
.contactTextareaWrapper {
  background: #ffffff03;
  min-height: 336px;
}
.contactTextareaInner {
  height: 336px;
  font-size: 18px;
  color: #fff;
}
.contactInputWrapper {
  background: #ffffff03;
  min-height: 78px;
}
.contactInputInner {
  height: 36px;
  font-size: 18px;
  color: #fff;
}
.contactPhonePrefix {
  color: #ffffff;
  font-weight: 600;
  font-size: 18px;
  line-height: 36px;
}
.contactActions {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.contactError {
  color: #ff7a7a;
  font-size: 14px;
}
.contactToast {
  position: fixed;
  inset: 24px 24px auto 24px;
  padding: 18px 24px;
  border-radius: 20px;
  background: rgba(20, 20, 20, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.08);
  opacity: 0;
  pointer-events: none;
  transform: translateY(-20px) scale(0.98);
  transition: opacity 0.4s ease, transform 0.5s ease;
  overflow: hidden;
  z-index: 120;
}
.contactToast.is-visible {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0) scale(1);
}
.contactToastGlow {
  position: absolute;
  inset: -40% 0 auto;
  height: 140%;
  background: radial-gradient(circle, rgba(200, 255, 44, 0.35) 0%, transparent 70%);
  animation: toastPulse 2.4s ease-in-out infinite;
}
.contactToastContent {
  position: relative;
  z-index: 1;
  color: #ffffff;
  display: grid;
  gap: 4px;
}
.contactToastTitle {
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 12px;
}
@keyframes toastPulse {
  0% {
    transform: translateY(10%) scale(0.95);
    opacity: 0.6;
  }
  50% {
    transform: translateY(-4%) scale(1.05);
    opacity: 1;
  }
  100% {
    transform: translateY(10%) scale(0.95);
    opacity: 0.6;
  }
}
.submitBtn {
  background: #c8ff2c;
  color: #000;
  height: 64px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 24px;
}
.footerContainer {
  background: #0B0B0B;
  padding: 32px;
  border-radius: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  height: 348px;
}
.footerContainer img {
  max-width: 506px;
  width: 100%;
  height: auto;
}
.footerContainer span {
  font-size: 16px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 960px) {
  .servicePageHero {
    grid-template-columns: 1fr;
  }
}
.serviceIntro {
  width: 100%;
  max-height: 1000px;
  height: 100vh;
  display: flex;
}
.serviceIntroCard {
  background: linear-gradient(120deg, #101010 20%, #c8ff2c 160%);
  border-radius: 24px;
  padding: 32px;
  color: #ffffff;
  gap: 24px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.serviceIntroHeader {
  display: flex;
  flex-direction: column;
  gap: 80px;
}
.serviceIntroHeader p {
  font-weight: 500;
  font-size: clamp(18px, calc(0.915vw + 14.43px), 32px);
  max-width: 1250px;
  width: 100%;
  background: linear-gradient(135deg, #555555 0%, #ffffff 35%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.serviceIntroEyebrow {
  color: #c8ff2c;
  font-weight: 600;
}
.serviceIntroFooter {
  max-width: 620px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.serviceIntroFooter h4 {
  color: #fff;
  font-size: clamp(18px, calc(0.915vw + 14.43px), 20px);
  font-weight: 500;
}
.serviceIntroFooter p {
  font-size: clamp(13px, calc(0.915vw + 14.43px), 16px);
  color: #ffffff80;
}
.serviceSolutions {
  display: grid;
  gap: 20px;
}
.serviceSolutionsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 18px;
}
.serviceSolutionsCard {
  position: relative;
  background: #101010;
  border-radius: 20px;
  padding: 24px;
  min-height: 536px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}
.serviceSolutionsCard::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 50% 120%, #c8ff2c, transparent 70%);
  opacity: 0.4;
  transition: opacity 0.4s ease, transform 0.4s ease;
  transform: translateY(10%);
  z-index: 0;
}
.serviceSolutionsCardContent {
  position: relative;
  z-index: 1;
  display: grid;
  gap: 12px;
  text-align: center;
  height: 100%;
}
.serviceSolutionsCardContent h4 {
  font-size: clamp(18px, calc(0.915vw + 14.43px), 20px);
}
.serviceSolutionsCardContent p {
  height: 100%;
  font-size: clamp(16px, calc(0.915vw + 14.43px), 20px);
}
.serviceSolutionsCard p {
  margin: 0;
  color: rgba(255, 255, 255, 0.7);
}
.serviceSolutionsCardButton {
  position: relative;
  z-index: 1;
  align-self: center;
  background: #f2f2f2;
  color: #111111;
  font-size: 14px;
  font-weight: 500;
  height: 46px;
  border-radius: 12px;
  transform: translateY(20px);
  opacity: 0;
  transition: transform 0.4s ease, opacity 0.4s ease, background 0.4s ease;
}
.serviceSolutionsCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.35);
}
.serviceSolutionsCard:hover::after {
  opacity: 0.9;
  transform: translateY(0);
}
.serviceSolutionsCard:hover .serviceSolutionsCardButton {
  transform: translateY(0);
  opacity: 1;
}
.serviceDrawerContent {
  width: min(560px, 100%);
  background: #111111;
  color: #ffffff;
  border-radius: 24px;
  padding: 0;
  height: 100vh;
}
.serviceDrawerHeader {
  padding: 15px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.serviceDrawerHeaderBlock {
  background: linear-gradient(120deg, #101010 20%, #8bb600 160%);
  border-radius: 18px;
  width: 100%;
  height: 200px;
  padding: 16px;
  display: flex;
  position: relative;
  flex-direction: row-reverse;
  background: linear-gradient(135deg, #cdff00 0%, #1e220d 50%, #cdff00 100%);
  background-size: 200% 200%;
  animation: serviceGradientFlow 6s ease infinite;
}
.serviceDrawerHeaderBlock h4 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 32px);
  text-align: center;
}
.serviceDrawerHeader h4 {
  margin: 0;
  font-size: 22px;
}
@keyframes serviceGradientFlow {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
.serviceDrawerClose {
  background: #ffffff;
  border-radius: 8px;
  width: 32px;
  height: 32px;
  cursor: pointer;
  min-width: auto;
}
.serviceDrawerBody {
  gap: 32px;
}
.serviceDrawerSection {
  display: flex;
  gap: 16px;
  flex-direction: column;
}
.serviceDrawerSection span {
  display: block;
  color: #fff;
}
.serviceDrawerSection p {
  margin: 0;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.5;
}
.serviceDrawerTags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.serviceDrawerTags span {
  background: #1f1f1f;
  padding: 8px 12px;
  border-radius: 12px;
  font-size: 14px;
}
.serviceDrawerFooter {
  padding: 16px;
}
.serviceDrawerCta {
  background: #f2f2f2;
  color: #111111;
  font-size: clamp(16px, calc(0.915vw + 14.43px), 20px);
  font-weight: 500;
  border-radius: 12px;
  height: 64px;
  width: 100%;
}
.serviceDrawerBackdrop {
  background: rgba(0, 0, 0, 0.35);
}
.serviceFaq {
  display: grid;
  gap: 20px;
}
.serviceFaqList {
  display: grid;
  gap: 12px;
}
.serviceFaqItem {
  background: #161616;
  border-radius: 18px;
  padding: 18px 22px;
  color: #ffffff;
}
.serviceFaqIndicator {
  display: block;
  width: 32px;
  height: 32px;
  transition: transform 0.3s ease;
  transform: rotate(0deg);
}
.serviceFaqItem[data-open="true"] .serviceFaqIndicator,
 .serviceFaqItem [data-open="true"] .serviceFaqIndicator {
  transform: rotate(180deg);
}
.serviceFaqItem p {
  margin: 12px 0 0;
  color: rgba(255, 255, 255, 0.75);
}
@media (max-width: 768px) {
  main {
    gap: 32px;
  }
  .serviceDrawerBackdrop {
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(8px);
  }
  .serviceDrawerContent {
    width: 100%;
    border-radius: 20px 20px 0 0;
    max-height: 85%;
  }
  .serviceDrawerHeader {
    padding: 8px;
  }
  .serviceDrawerHeaderBlock {
    height: 80px;
    align-items: center;
  }
  .serviceDrawerBody {
    padding: 8px;
  }
  .serviceDrawerHeaderBlock h4 {
    text-align: left;
  }
  .serviceDrawerCta {
    height: 54px;
  }
  .serviceSolutionsCardButton {
    transform: translateY(0);
    opacity: 1;
  }
  .contactsContainer {
    flex-direction: column;
  }
  .serviceCard {
    min-height: 377px;
    padding: 12px;
  }
  .serviceButton {
    height: 42px;
  }
  .heroContainer {
    padding: 12px;
  }
  .caseCard {
    padding: 12px;
    min-height: 377px;
  }
  .footerContainer {
    height: 160px;
  }
  body {
    padding: 8px;
  }
  .heroTitle {
    gap: 8px;
  }
  .dots {
    grid-template-rows: repeat(2, 6px);
    grid-template-columns: repeat(2, 6px);
    gap: 6px;
    margin-bottom: 4px;
  }
  .dots span {
    width: 6px;
    height: 6px;
  }
  .sectionTitle .dots {
    gap: 3px;
    margin: 0;
  }
  .sectionTitle .dots span {
    width: 4px;
    height: 4px;
  }
  .caseCard p {
    max-width: 326px;
  }
  .container {
    gap: 24px;
  }
  .submitBtn {
    border-radius: 12px;
  }
  .navMobDockList {
    display: flex;
  }
  .userInputs {
    flex-direction: column;
  }
  .serviceSolutionsCard {
    min-height: 240px;
  }
  .navDockList {
    display: none;
  }
  .navDockMenuButton {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  .serviceIntroHeader {
    gap: 32px;
  }
  .serviceFaqItem {
    padding: 16px;
  }
  button[data-slot="trigger"] {
    padding: 0;
  }
  .serviceIntroCard {
    padding: 16px;
  }
}
